import { Block, IBlock } from '../utils/Block';
import { trim } from '../utils/trim';

export interface BlockConstructable<
  P extends Record<string, unknown>,
  C extends Record<string, IBlock<Record<string, unknown>> | IBlock<Record<string, unknown>>[]>
> {
  new (props: P): Block<P, C>;
}

interface IRouter {
  use<
    C extends Record<string, IBlock<Record<string, unknown>> | IBlock<Record<string, unknown>>[]>,
    P extends Record<string, unknown> = Record<string, unknown>,
    B extends BlockConstructable<P, C> = BlockConstructable<P, C>
  >(
    path: Routes,
    block: B,
    props: P,
    query: string
  ): Router;
  start(): void; //  
  go(path: Routes): void;
  back(): void; //     
  forward(): void; //     
  onRoute(path: Routes): void;
}

interface IRoute {
  navigate(path: Routes): void;
  render(): void;
  match(pathname: Routes): boolean;
  leave(): void;
}

export enum Routes {
  SignInPage = '/',
  SignUpPage = 'sign-up',
  Chat = 'messenger',
  EditProfilePage = 'settings',
  EditPasswordPage = 'edit-password',
  Profile = 'profile',
  NotFoundPage = 'not-found',
  ServerError = 'server-error',
}

function render<P extends Record<string, unknown>>(query: string, block: IBlock<P>) {
  const root = document.querySelector(query);

  if (root === null) {
    throw new Error(`root not found by selector "${query}"`);
  }

  root.innerHTML = '';

  root.append(block.getContent()!);

  block.dispatchComponentDidMount();

  return root;
}

export class Route<
  C extends Record<string, IBlock<Record<string, unknown>> | IBlock<Record<string, unknown>>[]>,
  P extends Record<string, unknown> = Record<string, unknown>,
  B extends BlockConstructable<P, C> = BlockConstructable<P, C>
> implements IRoute
{
  public pathName: Routes;
  private blockClass: B;
  private block: Block<P, C> | null = null;
  private props: P;
  private readonly query: string;

  constructor(pathname: Routes, view: B, props: P, query: string) {
    this.pathName = pathname;
    this.blockClass = view;
    this.props = props;
    this.query = query;
  }

  navigate(pathName: Routes) {
    if (this.match(pathName)) {
      this.pathName = pathName;
      this.render();
    }
  }

  leave() {
    this.block = null;
  }

  match(pathname: Routes) {
    return pathname === this.pathName;
  }

  render() {
    if (!this.block) {
      this.block = new this.blockClass(this.props);
      render(this.query, this.block);

      return;
    }

    render(this.query, this.block);
  }
}

export class Router implements IRouter {
  private static instance: Router;
  private routes: IRoute[] = [];
  private history: (typeof window)['history'] = window.history;
  private currentRoute: IRoute | null = null;

  constructor(private readonly query: string) {
    if (Router.instance) {
      return Router.instance;
    }

    Router.instance = this;

    this.query = query;
  }

  use<
    C extends Record<string, IBlock<Record<string, unknown>> | IBlock<Record<string, unknown>>[]>,
    B extends BlockConstructable<P, C>,
    P extends Record<string, unknown> = Record<string, unknown>
  >(path: Routes, block: B, props: P): Router {
    const route = new Route(path, block, props, this.query);

    this.routes.push(route);

    return this;
  }

  start(): void {
    window.onpopstate = event => {
      const target = event.currentTarget as Window;
      const pathName =
        target.location.pathname === '/'
          ? Routes.SignInPage
          : (trim(target.location.pathname, '/') as Routes);
      this.onRoute(pathName);
    };
  }

  onRoute(pathname: Routes) {
    const route = this.getRoute(pathname);
    if (!route) {
      this.go(Routes.NotFoundPage);
      return;
    }

    if (this.currentRoute && this.currentRoute !== route) {
      this.currentRoute.leave();
    }

    this.currentRoute = route;

    route.render();
  }

  getRoute(pathname: Routes) {
    return this.routes.find(route => route.match(pathname)) ?? null;
  }

  go(pathname: Routes) {
    this.history.pushState({}, '', pathname);
    this.onRoute(pathname);
  }

  back() {
    this.history.back();
  }

  forward() {
    this.history.forward();
  }

  replace(newRoute: Routes) {
    this.history.replaceState({}, '', newRoute);
    this.onRoute(newRoute);
  }

  getCurrentPathName() {
    return trim(window.location.pathname, '/');
  }
}

export default new Router('#app');
