import AnchorLink from '../../components/AnchorLink/AnchorLink';
import { Button, ButtonStyleTypes } from '../../components/Button/Button';
import { Field } from '../../components/Field/Field';
import { Form, Props as FormProps } from '../../components/Form/Form';
import { Input } from '../../components/Input/Input';
import { AuthController } from '../../controllers/AuthController';
import { withControllers } from '../../hocs/withControllers';
import { Routes } from '../../Router/Router';
import { Block } from '../../utils/Block';
import template from './SignUpPage.hbs';

enum InputNames {
  Name = 'first_name',
  Surname = 'second_name',
  Login = 'login',
  Email = 'email',
  Phone = 'phone',
  Password = 'password',
}

type InputNamesType = {
  [InputNames.Name]: InputNames.Name;
  [InputNames.Surname]: InputNames.Surname;
  [InputNames.Login]: InputNames.Login;
  [InputNames.Email]: InputNames.Email;
  [InputNames.Phone]: InputNames.Phone;
  [InputNames.Password]: InputNames.Password;
};

const validator = (v: string, regExp: RegExp, errorText: string) => {
  const value = v.trim();
  const isValid = regExp.test(value);

  if (isValid) {
    return null;
  }

  return errorText;
};

const validationRules: FormProps<InputNamesType>['validationRules'] = {
  [InputNames.Name]: v =>
    validator(
      v,
      /^[A-Z-][\p{L}]*$/u,
      '  ,     ,     ,  '
    ),
  [InputNames.Surname]: v =>
    validator(
      v,
      /^[A-Z-][\p{L}]*$/u,
      '  ,     ,     ,  '
    ),
  [InputNames.Login]: v =>
    validator(
      v,
      /^[a-zA-Z][a-zA-Z0-9\-_]{3,20}$/,
      ' 3  20 , ,   ,     ,  ,   (    )'
    ),
  [InputNames.Email]: v =>
    validator(
      v,
      /^[\w-.]+@([\w-]+\.)+[\w-]{2,4}$/,
      ',       ,     (@)    ,       '
    ),
  [InputNames.Password]: v =>
    validator(
      v,
      /^(?=.*[a-z])(?=.*[A-Z])(?=.*\d)(?=.*[^\da-zA-Z]).{8,40}$/,
      ' 8  40 ,        '
    ),
  [InputNames.Phone]: v =>
    validator(
      v,
      /^\+?[0-9]{10,15}$/,
      ' 10  15 ,   ,    '
    ),
};

type Controllers = { auth: AuthController };
type AllProps = Controllers;

type Children = {
  signInLink: InstanceType<typeof AnchorLink>;
  form: Form<InputNamesType>;
};

class SignUpPage extends Block<AllProps, Children> {
  constructor(props: AllProps) {
    super(props);
  }

  init() {
    const nameInput = new Input({
      inputStyle: 'inputField__input',
      type: 'text',
      name: InputNames.Name,
    });
    const surnameInput = new Input({
      inputStyle: 'inputField__input',
      type: 'text',
      name: InputNames.Surname,
    });
    const loginInput = new Input({
      inputStyle: 'inputField__input',
      type: 'text',
      name: InputNames.Login,
    });
    const emailInput = new Input({
      inputStyle: 'inputField__input',
      type: 'email',
      name: InputNames.Email,
    });
    const phoneInput = new Input({
      inputStyle: 'inputField__input',
      type: 'tel',
      name: InputNames.Phone,
    });
    const passwordInput = new Input({
      inputStyle: 'inputField__input',
      type: 'password',
      name: InputNames.Password,
    });

    const inputs = [nameInput, surnameInput, loginInput, emailInput, phoneInput, passwordInput];

    const firstNameField = new Field({
      name: InputNames.Name,
      label: '',
      input: nameInput,
    });
    const secondNameField = new Field({
      name: InputNames.Surname,
      label: '',
      input: surnameInput,
    });
    const loginField = new Field({
      name: InputNames.Login,
      label: ' *',
      input: loginInput,
    });
    const emailField = new Field({
      name: InputNames.Email,
      label: 'Email *',
      input: emailInput,
    });
    const phoneField = new Field({
      name: InputNames.Phone,
      label: ' *',
      input: phoneInput,
    });
    const passwordField = new Field({
      name: InputNames.Password,
      label: ' *',
      input: passwordInput,
    });

    const fields: Field[] = [
      firstNameField,
      secondNameField,
      loginField,
      emailField,
      phoneField,
      passwordField,
    ];

    const submitBtn = new Button({
      label: '',
      stylesType: ButtonStyleTypes.Submit,
      styles: 'authForm__signUpBtn',
    });

    this.children.form = new Form<InputNamesType>({
      fields,
      submitBtn,
      submit: async values => {
        try {
          await this.props.auth.signUp(values);
        } catch (error) {
          const e = error as any;
          const reason = e.reason;

          submitBtn.props.actionError = reason;
        }
      },
      inputs,
      validationRules,
      formClass: 'authForm',
    });

    this.children.signInLink = new AnchorLink({
      href: '/',
      text: '  ?',
      styles: 'auth__link',
      path: Routes.SignInPage,
    });
  }

  protected render(): DocumentFragment {
    return this.compile(template, this.children);
  }
}

const WithControllers = withControllers<AllProps, Controllers, Children>(SignUpPage, {
  auth: new AuthController(),
});

export default WithControllers;
